body{
    padding-top: 5px;
    margin: 0;
    box-sizing: border-box;
    font-family: sans-serif;
    background-color: #fff;
}
.hom {
    text-align:center ;
    border: 1px;
    background-color: rgb(167, 238, 120);
    position: fixed;
    width: 100%;
    top: 0;
}
.hom li{
    display: inline-block;
    padding: 20px;
    font-size: 25px;
    font-style:normal;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}
.body{
    padding-top: 150px;

}
.container{
border: #f5f5f5;
align-items: center;
}
.cards{
    display:block;
    width:100;
    flex-wrap: wrap;
    justify-content: space-between;
    
}
.card{
    flex: 0 0 calc(33.33% - 20px);
    max-width: calc(60.33% - 20px);
    width: 100%;
    background:rgb(236, 231, 231);
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 20px;
  }
